/**
 * Modals
 */
export default function modal() {
    
    const modals = document.querySelectorAll('[data-js="modal"]');

    if (modals) {

        modals.forEach(item => {

            const name = item.dataset.modalname;
            const modalOpener = document.querySelector(`[data-modalOpen="${name}"]`);
            const modalCloser = item.querySelector('.close');
            
            modalOpener.addEventListener('click', () => {
                openModal(item);
            });

            modalCloser.addEventListener('click', () => {
                closeModal(item);
            });

        });

    }

}

const transitionTime = 750;

function openModal(modal) {

    modal.style.display = 'flex';
    setTimeout(() => {
        modal.classList.add('show');
    }, 50);

}

function closeModal(modal) {

    modal.classList.remove('show');
    setTimeout(() => {
        modal.style.display = 'none';
    }, transitionTime);

}